body {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    background-color: rgba(255, 230, 215, 1);
    font-family: Cormorant;
    font-size: 18px;
    font-weight: 500;
    line-height: 28px;

}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}